@import url(https://fonts.googleapis.com/css?family=PT+Sans&display=swap);
$font-primary: 'PT Sans', sans-serif !default;

$brand-primary: #00afe5;
$brand-secondary: #f94b79;

$gray-dark: #333;
$gray: #999;
body {
	margin: 0;
	font-family: $font-family;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

header {
  position: relative;
}
header.bg {
  background-color: white;
}
header.bg button {
  background-color: #999;
}
header .hamburger {
  position: absolute;
}
header .signin-button {
  color: white;
}
header .quiz-button {
  background-color: #00afe5 !important;
  font-family: "PT Sans", sans-serif;
  margin-right: 8px;
  color: white;
  font-weight: 600;
}
header .header-logo {
  flex-grow: 1;
  margin: 0 auto;
  display: block;
}
header .header-logo img {
  height: 50px;
}
header .right-menu {
  position: absolute;
  top: 14px;
  right: 24px;
}

main.experience h3 {
  color: #00afe5;
  text-align: center;
  width: 100%;
  line-height: 48px;
  font-size: 32px;
}
.home {
  background-color: #eee;
  flex-grow: 0;
}
.home .background {
  width: 100%;
  height: 480px;
  background-image: url(/boite/static/media/AdobeStock_259547347.a34b23fd.jpg);
  background-repeat: no-repeat;
  background-attachment: scroll;
  background-position: top;
  background-size: cover;
  text-align: left;
}
.home .background > div {
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.home .background h2 {
  text-align: left;
  font-family: "PT Sans", sans-serif;
  font-size: 56px;
  letter-spacing: -1px;
  font-weight: 400;
  text-transform: none;
  color: white;
}
.home .background .button {
  margin-top: 8px;
  background-color: #00afe5;
  font-family: "PT Sans", sans-serif;
  font-size: 20px;
  text-transform: none;
  text-decoration: none;
}
.home h3 {
  text-align: left;
  font-family: "PT Sans", sans-serif;
  font-size: 30px;
  letter-spacing: -1px;
  font-weight: 400;
  text-transform: none;
  color: white;
  margin: 0;
  line-height: 46px;
  line-height: 40px;
  margin: 8px 0;
  width: 90%;
}
.home .offer-boxes {
  font-family: "PT Sans", sans-serif;
  background-color: #333;
  padding: 16px 0;
  text-align: center;
  border-radius: 0;
}
.home .offer-boxes .offer-icons {
  width: 50px;
}
.home .offer-boxes ul {
  display: flex;
  flex-direction: row;
  text-align: left;
  color: #eee;
  align-items: center;
  border-right: 1px solid #666;
}
.home .offer-boxes ul li {
  line-height: 20px;
}
.home .offer-boxes ul li h4 {
  font-size: 18px;
  font-weight: 600;
}
.home .offer-boxes ul li h5 {
  font-size: 12px;
}
.home .offer-boxes ul li:first-child {
  margin-right: 12px;
}
.home .offer-boxes .offer-boxes-container > div:last-child ul {
  border-right: none;
}
.home .offer-boxes h4 {
  font-size: 16px;
  font-weight: 500;
  color: #eee;
}
.choice {
  float: none;
  margin: 0;
}
.choice .answer-container {
  position: relative;
}
.choice .answer-container .material-icons {
  position: absolute;
  top: 40px;
  left: calc(50% - 16px);
  font-size: 32px;
}
.choice .check {
  font-size: 30px;
  font-size: 1.875rem;
  top: calc(50% - 26px);
  left: calc(50% - 15px);
}
.choice .swatch {
  width: 100px;
  height: 100px;
  border-radius: 50px;
  margin: 10px auto;
}
.choice .swatch.image {
  background-size: cover;
}
.choice .swatch.big-image {
  width: 161px;
  height: 255px;
  border-radius: 0;
  background-size: contain;
}
.choice .swatch.icon {
  background-size: cover;
  border-radius: 0;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: bottom;
}
.choice .label {
  text-transform: uppercase;
  text-align: center;
  font-size: 12px;
}
.choice .label.only-value {
  font-size: 18px;
  border: 1px solid #00afe5;
  padding: 20px 16px 32px;
}
.choice span.MuiButtonBase-root:hover {
  background-color: transparent !important;
}

.MuiRadio-colorSecondary.Mui-checked:hover {
  border-radius: 0 !important;
}
.back-next {
  margin: 64px 0;
}
.back-next a button {
  width: 100%;
}
.page.quiz h2 {
  margin: 32px 0 64px;
  text-align: center;
}
.page.quiz .buttons {
  margin-top: 64px;
}
.page.quiz .MuiRadio-colorSecondary.Mui-checked:hover {
  border-radius: 0;
}
.page.quiz .MuiFormGroup-root {
  justify-content: center;
}
.register .background {
  width: 100%;
  background-image: url(/boite/static/media/AdobeStock_187335069.40892583.jpg);
  background-repeat: no-repeat;
  background-attachment: scroll;
  background-position: top;
  background-size: cover;
  text-align: left;
}
.register .background h2 {
  margin: 65px 0 0;
  text-align: left;
  font-family: "PT Sans", sans-serif;
  font-size: 56px;
  letter-spacing: -1px;
  font-weight: 400;
  text-transform: none;
  color: #f94b79;
  display: inline-block;
}
.register .background h3 {
  text-align: left;
  font-family: "PT Sans", sans-serif;
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -1px;
  font-weight: 400;
  text-transform: none;
  color: #333;
  padding: 16px 0;
  margin: 0;
}
.register .container {
  display: flex;
  flex-wrap: wrap;
  position: relative;
}
.register .container > div {
  margin-right: 16px;
  flex-grow: 2;
}
.register .address-suggestions-container {
  width: 300px;
  position: relative;
}
.register .textfield {
  position: relative;
}
.page.checkout .cart-background {
  background-image: url(/boite/static/media/AdobeStock_263790084.272f8982.png);
  background-repeat: no-repeat;
  background-attachment: scroll;
  background-position: center;
  background-size: contain;
  text-align: left;
}
.page.checkout h2 {
  font-family: "PT Sans", sans-serif;
  font-size: 56px;
  letter-spacing: -1px;
  font-weight: 400;
  text-transform: none;
  color: #f94b79;
  margin: 32px 0 0;
  text-align: center;
}
.page.checkout h3 {
  line-height: 21px;
  margin: 8px 0 0;
}
.page.checkout .content-container {
  background-color: #ddc7ca;
}
.about {
  background-color: #eee;
  flex-grow: 0;
}
.about .background {
  width: 100%;
  height: 800px;
  background-image: url(/boite/static/media/about.a660cf19.jpg);
  background-repeat: no-repeat;
  background-attachment: inherit;
  background-position: center;
  background-size: cover;
  text-align: center;
}
.about .background > div {
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.about .background .about-content-container {
  background-color: rgba(255, 255, 255, 0.4);
  padding: 32px;
  border-radius: 4px;
}
.about .background h2 {
  font-family: "PT Sans", sans-serif;
  font-size: 45px;
  letter-spacing: -1px;
  font-weight: 400;
  text-transform: none;
  color: #00afe5;
}
.about .background p {
  font-family: "PT Sans", sans-serif;
  font-size: 16px;
  font-weight: 400;
  text-transform: none;
  color: #333;
  margin: 16px 0;
  line-height: 28px;
}
.about .background p strong {
  font-weight: 600;
}
.about .background .button {
  margin-top: 8px;
  background-color: #f94b79;
  font-family: "PT Sans", sans-serif;
  font-size: 20px;
  text-transform: none;
  text-decoration: none;
}
.why {
  background-color: #eee;
  flex-grow: 0;
}
.why .background {
  width: 100%;
  height: 560px;
  background-image: url(/boite/static/media/why.41596b4b.jpg);
  background-repeat: no-repeat;
  background-attachment: inherit;
  background-position: left;
  background-size: contain;
  text-align: left;
  background-color: #f7a3a1;
}
.why .background > div {
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.why .background > div.right-content {
  background-color: rgba(255, 255, 255, 0.1);
  padding-left: 32px;
}
.why .background h2 {
  text-align: left;
  font-family: "PT Sans", sans-serif;
  font-size: 45px;
  letter-spacing: -1px;
  font-weight: 400;
  text-transform: none;
  color: white;
  line-height: 56px;
}
.why .background h3, .why .background ul {
  font-family: "PT Sans", sans-serif;
  text-align: left;
  font-size: 19px;
  letter-spacing: -1px;
  font-weight: 400;
  text-transform: none;
  color: #333;
  margin: 8px 0;
  line-height: 32px;
  width: 90%;
}
.why .background h3 strong, .why .background ul strong {
  font-weight: 600;
}
.why .background .button {
  margin-top: 8px;
  background-color: #f94b79;
  font-family: "PT Sans", sans-serif;
  font-size: 20px;
  text-transform: none;
  text-decoration: none;
}
.events {
  background-color: #eee;
  flex-grow: 0;
}
.events .background {
  width: 100%;
  height: 260px;
  background-image: url(/boite/static/media/sorteo.27e7cd12.jpeg);
  background-repeat: no-repeat;
  background-attachment: scroll;
  background-position: center;
  background-size: cover;
  text-align: center;
}
.events .background > div {
  display: flex;
  justify-content: center;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0.6);
}
.events .background h2 {
  font-family: "PT Sans", sans-serif;
  font-size: 56px;
  letter-spacing: -1px;
  font-weight: 400;
  text-transform: none;
  color: #f94b79;
}
.events .background .button {
  margin-top: 8px;
  background-color: #00afe5;
  font-family: "PT Sans", sans-serif;
  font-size: 20px;
  text-transform: none;
  text-decoration: none;
}
.events h3 {
  font-family: "PT Sans", sans-serif;
  font-size: 30px;
  letter-spacing: -1px;
  font-weight: 400;
  text-transform: none;
  margin: 0;
  line-height: 46px;
  line-height: 40px;
  margin: 8px 0;
  color: #333;
}
.events .content {
  margin: 32px 0;
}
.events .content p {
  line-height: 24px;
  color: #999;
}
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before, blockquote:after {
  content: "";
  content: none;
}

q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.page.quiz h2 {
  margin: 32px 0 64px;
  text-align: center;
}
.page.quiz .buttons {
  margin-top: 64px;
}
.page.quiz .MuiRadio-colorSecondary.Mui-checked:hover {
  border-radius: 0;
}
.page.quiz .MuiFormGroup-root {
  justify-content: center;
}

html {
  background-color: #eee;
}

body {
  font-family: "PT Sans", sans-serif;
}
body .page {
  text-align: center;
}

a {
  text-decoration: none;
}

.items-container {
  justify-content: center;
}

h2 {
  font-family: "PT Sans", sans-serif;
  font-size: 56px;
  letter-spacing: -1px;
  font-weight: 400;
  text-transform: none;
  color: #f94b79;
}

.page {
  margin-top: 65px;
  position: relative;
  overflow: hidden;
}

.App-logo {
  height: 50px;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
